import React from 'react';
import {FieldError} from 'react-hook-form';

interface DisplayFormErrorProps {
    error: FieldError | undefined;
    fieldDisplayName?: string; // Display name of the field
}

const DisplayFormError: React.FC<DisplayFormErrorProps> = ({
    error,
    fieldDisplayName
}) => {
    if (!error) {
        return null;
    }

    let message = error.message;

    if (!message && error.type === 'required') {
        message = `${fieldDisplayName} is required`;
    }

    return <div className="text-danger small">{message}</div>;
};

export default DisplayFormError;
